import Vue from 'vue'
import Router from 'vue-router'
import Member from '../components/member'
import Todo from '../components/Todo'
import Home from '../components/Home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/member',
      name: 'member',
      component: Member
    },
    {
      path: '/Todo',
      name: 'Todo',
      component: Todo
    }
  ]
})



